<template>
  <div>
    <div v-if="$store.state.isMobile">
      <video-player
        class="video-player vjs-custom-skin"
        ref="videoPlayer"
        x5-video-player-type="h5"
        x-webkit-airplay="true"
        webkit-playsinline="true"
        x5-video-player-fullscreen="true"
        x5-video-orientation="portraint"
        :playsinline="true"
        :options="playerOptions"
        style="height:100vh !important"
      >
      </video-player>
      <!-- <div class="dark_cover"></div> -->

      <!-- <div
        v-for="(item, index) in imglist"
        :key="index"
        class="mobile-home-img"
      >
        <div class="swipe-text">
          <p>{{ item.title }}</p>
          <p>{{ item.content }}</p>
        </div>
        <a :href="item.img_link" exact v-if="item.img_link">
          <el-image :src="item.img_path" fit="cover"></el-image>
        </a>
        <el-image v-else :src="item.img_path" fit="cover"></el-image>
      </div> -->
    </div>
    <swiper :options="swiperOption" ref="mySwiper" v-else>
      <!-- slides -->
      <swiper-slide v-for="(item, index) in imglist" :key="index">
        <div class="swipe-text">
          <p>{{ item.title }}</p>
          <p>{{ item.content }}</p>
        </div>
        <a :href="item.img_link" exact v-if="item.img_link">
          <img :src="item.img_path" />
        </a>
        <img v-else :src="item.img_path" />
      </swiper-slide>

      <!-- Optional controls -->
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
// 播放器-导入组件（在要用组件的页面导入组件,并声明）
import { UNI_ALUB_IMDEX, GET_FOOTER } from "@/api/mall";
import { videoPlayer } from "vue-video-player";
import WeixinJSBridge from "weixin-js-sdk";

export default {
  data() {
    return {
      //视频
      imglist: [],
      swiperOption: {
        direction: "vertical",
        slidesPerView: "auto",
        mousewheel: {
          releaseOnEdges: true
        },
        effect: "fade", // speed: 300,
        autoplay: {
          delay: 3000
        },
        resistanceRatio: 0,
        observeParents: true,
        autoHeight: true,
        height: window.innerHeight,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      screenWidth: "",
      screenHeight: "",
      playerOptions: {
        playbackRates: 1.0, //播放速度
        autoplay: false, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: true, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: "video/mp4",
            src: "" //视频url地址
            //imgUrl:require("../assets/test.png")
          }
        ],
        poster: "", //你的封面地址
        // width: document.documentElement.clientWidth,
        notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: false,
          durationDisplay: false,
          remainingTimeDisplay: false,
          fullscreenToggle: false //全屏按钮
        }
      }
    };
  },
  name: "top",
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    },
    player() {
      return this.$refs.videoPlayer.player;
    }
  },
  beforeCreate() {
    this.$emit("header", true);
    this.$emit("footer", true);
  },
  watch: {
    screenWidth: function() {
      this.swiperOption.height = document.body.clientHeight;
    }
  },
  methods: {
    getbanner() {
      console.log(this.$store.state.isMobile, "$store.state.isMobile");
      UNI_ALUB_IMDEX({
        type: this.$store.state.isMobile
      })
        .then(res => {
          this.imglist = res.data;
          this.playerOptions.poster = res.data[0].media_cover;
          this.playerOptions.sources[0].src = res.data[0].media_path;
          console.log(res);
        })
        .catch(err => {});
    }
  },
  mounted() {
    this.getbanner();
    this.screenWidth = document.body.clientWidth;
    this.screenHeight = document.body.clientHeight;
    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth;
        this.swiperOption.height = document.body.clientHeight;
      })();
    };
    this.$nextTick(function() {
      //先暂停播放
      this.$refs.videoPlayer.player.pause();
      //然后播放
      this.$refs.videoPlayer.player.play();
      //主要起决定性代码部分
      if (
        typeof WeixinJSBridge == "object" &&
        typeof WeixinJSBridge.invoke == "function"
      ) {
        WeixinJSBridge.invoke("getNetworkType", {}, function() {
          this.$refs.videoPlayer.player.play();
        });
      }
      document.addEventListener(
        "WeixinJSBridgeReady",
        () => {
          this.$refs.videoPlayer.player.play();
        },
        false
      );
    });
  }
};
</script>
<style lang="scss" scoped type="text/scss">
// .swiper-container {
//   width: 100%;
//   height: 100%;
//   margin-left: auto;
//   margin-right: auto;
// }
.swiper-slide {
  height: calc(100vh - 50px);
}
.swiper-slide-active {
  height: auto;
}
.swipe-text {
  position: absolute;
  top: 45%;
  right: 5%;
  z-index: 9;
  p {
    color: white;
    padding-bottom: 15px;
    text-align: right;
    &:nth-child(1) {
      font-size: 38px;
      font-weight: bold;
    }
    &:nth-child(2) {
      font-size: 30px;
    }
  }
}
img {
  width: 100%;
}
.mobile-home-img .el-image {
  width: 100%;
}
.physical-video {
  width: 100%;
}
</style>
<style rel="stylesheet/scss" lang="scss">
.video-js.vjs-fluid {
  height: 100vh !important;
  .vjs-tech {
    object-fit: cover;
  }
}
.video-player,
.vjs-custom-skin {
  width: 100%;
  height: 100vh !important;
  position: relative;
  left: 0;
  top: 0;
  z-index: 12;
}
.vjs-control-bar {
  display: none !important;
}
</style>



